import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import "../../css/RestaurantDetail.css";

const ReviewModal = ({ reviews, onClose }) => {
    return (
        <div className="modal">
            <div className="modal-content">
                <button className="close-modal" onClick={onClose}>X</button>
                <h3>리뷰 전체 보기</h3>
                <div className="modal-reviews">
                    {reviews.map((review, index) => (
                        <div key={index} className="modal-review-item">
                            <p>
                                <strong>{review.nickname}</strong>{" "}
                                <span className="rating">{review.rating}</span>
                            </p>
                            <p>{review.content}</p>
                            <div className="review-actions">
                                ❤️ 좋아요 {review.likes} 댓글 {review.comments}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

const RestaurantDetail = () => {
    const { name } = useParams();
    const [restaurant, setRestaurant] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [error, setError] = useState("");

    useEffect(() => {
        axios
            .get(`http://localhost:18080/api/restaurants/${name}`)
            .then((response) => {
                setRestaurant(response.data);
                setReviews(response.data.reviews || []); // 리뷰 데이터 설정
            })
            .catch((err) => setError(err.message));
    }, [name]);

    if (error) {
        return <div>오류: {error}</div>;
    }

    if (!restaurant) {
        return <div>로딩 중...</div>;
    }

    const openModal = () => {
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
    };

    return (
        <div className="restaurant-detail">
            <div className="top-section">
                <div className="image-box">
                    <img
                        src={
                            restaurant.menuName === "default.jpg"
                                ? "/images/default.jpg"
                                : `/images/menu/${restaurant.menuName}`
                        }
                        alt={restaurant.name}
                    />
                </div>
                <div
                    className="map-box"
                    style={{ height: "320px", overflow: "hidden", position: "relative" }}
                >
                    <iframe
                        src={`https://map.naver.com/v5/search/${encodeURIComponent(
                            restaurant.address
                        )}`}
                        width="230%"
                        height="100%"
                        style={{
                            border: "none",
                            position: "absolute",
                            top: 0,
                            left: "-130%",
                        }}
                        allowFullScreen
                        title="Naver Map"
                    ></iframe>
                </div>
            </div>
            <div className="title-and-actions">
                <h2>
                    {restaurant.name}{" "}
                    <span className="rating">{restaurant.scoreNumber}</span>
                </h2>
                <div className="actions">
                    <span>⭐ 즐겨찾기</span>
                    <span>❤️ 좋아요 {restaurant.likeCount || 0}</span>
                    <span>👀 조회수 {restaurant.viewCount || 0}</span>
                </div>
            </div>
            <div className="main-section">
                <div className="details">
                    <p>영업시간: {restaurant.openTime || "정보 없음"}</p>
                    <p>휴게시간: {restaurant.breakTime || "정보 없음"}</p>
                    <p>휴무일: {restaurant.offDays || "정보 없음"}</p>
                    <p>
                        주소: {restaurant.address || "정보 없음"} (지번:{" "}
                        {restaurant.oldAddress || "정보 없음"})
                    </p>
                    <p>전화번호: {restaurant.phoneNumber || "정보 없음"}</p>
                    {restaurant.homepage &&
                    restaurant.homepage !== "홈페이지 정보 없음" ? (
                        <p>
                            홈페이지:{" "}
                            <a
                                href={
                                    restaurant.homepage.startsWith("http")
                                        ? restaurant.homepage
                                        : `https://${restaurant.homepage}`
                                }
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                {restaurant.homepage}
                            </a>
                        </p>
                    ) : (
                        <p>홈페이지: 정보 없음</p>
                    )}
                    <p>주차: {restaurant.parkingInfo || "정보 없음"}</p>
                    <p>관련 태그: {restaurant.tags || "정보 없음"}</p>
                </div>
                <div className="reviews-section">
                    <div className="reviews-header">
                        <h3>리뷰 ({reviews.length})</h3>
                        <button className="review-button">리뷰 작성 ∨</button>
                    </div>
                    <div className="review-list">
                        {reviews.slice(0, 3).map((review, index) => (
                            <div key={index} className="review-item">
                                <p>
                                    닉네임{" "}
                                    <span className="rating">{review.rating}</span>
                                </p>
                                <div className="review-content">{review.content}</div>
                                <div className="review-image">이미지</div>
                                <div className="review-actions">
                                    ❤️ 좋아요 {review.likes}
                                </div>
                            </div>
                        ))}
                    </div>
                    <button className="more-reviews" onClick={openModal}>
                        리뷰 더보기 +
                    </button>
                </div>
            </div>

            {isModalOpen && <ReviewModal reviews={reviews} onClose={closeModal} />}
        </div>
    );
};

export default RestaurantDetail;